@import url("https://fonts.googleapis.com/css?family=Nunito:300,400,700,800&display=swap");
@import "./assets/css/font-awesome.css";
@import "./assets/css/animate.css";
@import "./assets/css/bootstrap.min.css";
@import "./assets/css/default.css";

body {
	font-family: "Nunito", sans-serif;
	font-weight: normal;
	font-style: normal;
	color: #798795;
	overflow-X: hidden;
}

#root {
	display: flex;
	min-height: 100vh;
	height: 100%;
	flex-direction: column;
	justify-content: space-between;
	background-image: url("./assets/images/header-hero.jpg");
}

* {
	margin: 0;
	padding: 0;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}

img {
	max-width: 100%;
}

a:focus,
input:focus,
textarea:focus,
button:focus {
	text-decoration: none;
	outline: none;
}

a:focus,
a:hover {
	text-decoration: none;
}

i,
span,
a {
	display: inline-block;
}

audio,
canvas,
iframe,
img,
svg,
video {
	vertical-align: middle;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: "Nunito", sans-serif;
	font-weight: 800;
	color: #404040;
	margin: 0px;
}

h1 {
	font-size: 48px;
}

h2 {
	font-size: 36px;
}

h3 {
	font-size: 28px;
}

h4 {
	font-size: 22px;
}

h5 {
	font-size: 18px;
}

h6 {
	font-size: 16px;
}

ul,
ol {
	margin: 0px;
	padding: 0px;
	list-style-type: none;
}

p {
	font-size: 14px;
	font-weight: 400;
	line-height: 24px;
	color: #798795;
	margin: 0px;
	font-family: "Nunito", sans-serif;
}

.bg_cover {
	background-position: center center;
	background-size: cover;
	background-repeat: no-repeat;
	width: 100%;
	height: 100%;
}

.m-auto {
	margin: 0 0 0 auto !important;
}


/*===== All Button Style =====*/

.main-btn {
	display: inline-block;
	font-weight: 700;
	font-family: "Nunito", sans-serif;
	text-align: center;
	white-space: nowrap;
	vertical-align: middle;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	border: 2px solid #f14836;
	padding: 0 30px;
	font-size: 16px;
	height: 55px;
	line-height: 51px;
	border-radius: 50px;
	color: #fff;
	cursor: pointer;
	z-index: 5;
	-webkit-transition: all 0.4s ease-out 0s;
	-moz-transition: all 0.4s ease-out 0s;
	-ms-transition: all 0.4s ease-out 0s;
	-o-transition: all 0.4s ease-out 0s;
	transition: all 0.4s ease-out 0s;
	background-color: #f14836;
}

.main-btn:hover {
	background-color: rgba(241, 72, 54, 0.3);
	color: #f14836;
	border-color: #f14836;
}

.main-btn.main-btn-2 {
	background-color: #fff;
	color: #f14836;
	border-color: #f14836;
}

.main-btn.main-btn-2:hover {
	background-color: rgba(241, 72, 54, 0.3);
	border-color: #f14836;
}


/*===== All Section Title Style =====*/

.section-title .sub-title {
	font-size: 18px;
	font-weight: 400;
	color: #f14836;
	text-transform: uppercase;
}

@media (max-width: 767px) {
	.section-title .sub-title {
		font-size: 16px;
	}
}

.section-title .title {
	font-size: 32px;
	padding-top: 10px;
}

@media only screen and (min-width: 992px) and (max-width: 1199px) {
	.section-title .title {
		font-size: 30px;
	}
}

@media (max-width: 767px) {
	.section-title .title {
		font-size: 24px;
	}
}

.section-title .title span {
	font-weight: 400;
	display: contents;
}

.headroom {
	will-change: transform;
	transition: transform 200ms linear;
}

.headroom--not-top {
	transform: translateY(0%);
	position: fixed;
	z-index: 99;
	background-color: #fff;
	-webkit-box-shadow: 0px 20px 50px 0px rgba(0, 0, 0, 0.05);
	-moz-box-shadow: 0px 20px 50px 0px rgba(0, 0, 0, 0.05);
	box-shadow: 0px 20px 50px 0px rgba(0, 0, 0, 0.05);
	-webkit-transition: all 0.3s ease-out 0s;
	-moz-transition: all 0.3s ease-out 0s;
	-ms-transition: all 0.3s ease-out 0s;
	-o-transition: all 0.3s ease-out 0s;
	transition: all 0.3s ease-out 0s;
}

.headroom--not-top .navbar {
	padding: 10px 0;
}

.headroom--unpinned {
	transform: translateY(-100%);
}

.navbar {
	padding: 25px 0;
	border-radius: 5px;
	position: relative;
	-webkit-transition: all 0.3s ease-out 0s;
	-moz-transition: all 0.3s ease-out 0s;
	-ms-transition: all 0.3s ease-out 0s;
	-o-transition: all 0.3s ease-out 0s;
	transition: all 0.3s ease-out 0s;
}

.header-hero {
	height: 800px;
	position: relative;
}

@media only screen and (min-width: 1400px) {
	.header-hero {
		height: 990px;
	}
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
	.header-hero {
		height: auto;
	}
}

@media (max-width: 767px) {
	.header-hero {
		height: auto;
	}
}

.content-title .welcome {
	font-size: 18px;
	font-weight: 400;
	text-transform: uppercase;
	letter-spacing: 5px;
	color: #f14836;
	font-family: "Nunito", sans-serif;
}

@media (max-width: 767px) {
	.content-title .welcome {
		font-size: 16px;
	}
}

.content-title .title {
	padding-top: 10px;
	font-weight: 400;
	font-size: 32px;
}

@media (max-width: 767px) {
	.content-title .title {
		font-size: 24px;
	}
}

.content-title .title span {
	font-weight: 700;
}